import React from 'react';
import _ from 'lodash';
import classnames from 'classnames';

import {
  ComboBox,
  Modal,
} from 'ui';

import styles from './App.less';

const values = [
  ' ',
  ' ',
  ' ',
  ' ',
  ' ',
  ' ',
  ' ',
  ' ',
  ' ',
  ' ',
]

function getSource(input) {
  const inputInLowerCase = input.toLowerCase();
	return Promise.resolve({
		values: _.filter(values, (d, i) => _.includes(d, inputInLowerCase))
	})
}

function foundItem(item) {
  return <span>{item}</span>;
}

export default class App extends React.Component {
  state = {
    value: ''
  }

  render(){
    return <div className={styles.main}>
      <h3>:</h3>
      <p>Combobox    hover.       Combobox`, input .</p>
      <p>,   ,       .</p>
      <div className={styles.block}>
        <span className={styles.text}>{this.state.value || '  ' }</span>
        <div className={styles.combobox}>
          <ComboBox
            value={this.state.value}
            onChange={({target: {value}}) => this.setState({value})}
            renderValue={(value, info) => <span>{value}</span>}
            source={getSource}
            renderItem={foundItem}
            placeholder=" "
            openButton
          />
        </div>
      </div>
    </div>
  }
}



